<template>
	<div class="form">
		<h2>{{$t('form.header')}}</h2>
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="ruleForm slideUp">
  <el-form-item prop="name">
    <el-input v-model="ruleForm.name" placeholder="First Name"></el-input>
  </el-form-item>
    <el-form-item prop="surname">
    <el-input v-model="ruleForm.surname" placeholder="Last Name"></el-input>
  </el-form-item>
  <el-form-item prop="email">
    <el-input v-model="ruleForm.email" placeholder="Email"></el-input>
  </el-form-item>
  <el-form-item prop="phone">
    <el-input v-model.number="ruleForm.phone" placeholder="Work Phone"></el-input>
  </el-form-item>
    <el-form-item prop="company">
    <el-input v-model="ruleForm.company" placeholder="Company"></el-input>
  </el-form-item>
    <el-form-item>
    <el-select v-model="ruleForm.interested" clearable filterable multiple collapse-tags placeholder="I'm interested in">
    <el-option
      v-for="item in ruleForm.interests"
      :key="item.category"
      :value="item.category">
    </el-option>
  </el-select>
  </el-form-item>
  <el-form-item>
    <el-select v-model="ruleForm.value" clearable filterable placeholder="Country">
    <el-option
      v-for="item in ruleForm.regions"
      :key="item.value"
      :value="item.value">
    </el-option>
  </el-select>
  </el-form-item>
  <el-form-item>
    <el-checkbox label="Send NDA" v-model="ruleForm.checked" name="nda"></el-checkbox>
  </el-form-item>
  <el-form-item prop="checked1">
    <el-checkbox label="I consent to having Besttoolbars collect my personal details." v-model="ruleForm.checked1" name="details"></el-checkbox>
  </el-form-item>
  <el-form-item prop="desc">
    <el-input type="textarea" v-model="ruleForm.desc" placeholder="Project description"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" :loading="sending" @click="submitForm('ruleForm')">{{$t('form.submit')}}</el-button>
    <el-button @click="resetForm('ruleForm')">{{$t('form.reset')}}</el-button>
  </el-form-item>
</el-form>
	</div>
</template>
<script>
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
          surname: '',
          email: '',
          phone: '',
          company: '',
          regions: [{
          value: 'Russia'
        }, {
          value: 'USA'
        }, {
          value: 'Italy'
        }, {
          value: 'Spain'
        }, {
          value: 'Czech Republic'
        }],
          interests: [{
    "category": "Cashback business"
  }, {
    "category": "Coupons business"
  }, {
    "category": "Price Comparison business"
  }, {
    "category": "Rewards or Charity business"
  }, {
    "category": "Mobile Apps Developmen"
  }, {
    "category": "Custom Development"
  }],
          delivery: false,
          checked: false,
          checked1: [],
          desc: '',
          value: '',
          interested: ''
        },
        rules: {
          name: [
            { required: true, message: 'Please input your name', trigger: 'blur' }
          ],
          surname: [
            { required: true, message: 'Please input your surname', trigger: 'blur' },
          ],
          email: [
            { required: true, message: 'Please input email address', trigger: 'blur' },
            { type: 'email', message: 'Please input correct email address', trigger: ['blur', 'change'] }
          ],
          phone: [
            { required: true, message: 'Please input phone number'},
            { type: 'number', message: 'Phone must be a number'}
          ],
          region: [
            { required: true, message: 'Please select Activity zone', trigger: 'change' }
          ],
          checked1: [
            { type: 'array', required: true, message: 'Please сonfirm your consent to the processing of personal data', trigger: 'change' }
          ]
        },
        sending: false
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.sending = true;
            fetch('http://54.187.233.30/', {
              method: 'POST',
              body: JSON.stringify(this.ruleForm),
              headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              },
            })
            .then(res => {
              this.sending = false;
              if (res.status === 200 ) {
                  this.$notify({
                  title: 'Success',
                  message: 'This is a success message',
                  type: 'success'
                });
              }
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
  }
</script>

<style lang="scss" scoped>

</style>
