import Vue from 'vue'
import Vuex from 'vuex'
import vuexI18n  from 'vuex-i18n'
import App from './App.vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css'
import locale from 'element-ui/lib/locale/lang/en'
import router from './router'

Vue.use(Vuex);
const store = new Vuex.Store();
Vue.use(vuexI18n.plugin, store);
Vue.config.productionTip = false;
Vue.use(ElementUI, { locale });

fetch('http://www.softomate.net/ext/WPN/localization.json').then(response => response.json()).then(data => {
  const translationEn = data[0].en;
  const translationCh = data[1].ch;
  Vue.i18n.add('en', translationEn);
  Vue.i18n.add('ch', translationCh);
  Vue.i18n.set(localStorage.getItem('BTB_locale') || 'en');
  new Vue({
    router,
    render: h => h(App)
  }).$mount('#app')
})
